/* ---------- Home: Speech Bubble ---------- */
.siru-speech-bubble {
  position: relative;
  display: inline-block;
  background: #ffffff;
  color: #2c3e50;
  border-radius: 999px;
  padding: 0.8rem 2rem;
  font-family: 'Cafe24Meongi', sans-serif;
  font-size: 1rem;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.1);
  margin: 1.5rem auto;
  max-width: 500px;
  animation: floatTalk 2s ease-in-out infinite;
  text-align: center;
}

.siru-speech-bubble::after {
  content: "";
  position: absolute;
  bottom: -12px;
  left: 50px;
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 12px solid #ffffff;
}

@keyframes floatTalk {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px);
  }
}

/* ---------- Instagram Section ---------- */
.instagram-section {
  padding: 2rem 1rem;
  text-align: center;
  background: #fffdf4;
}

.instagram-section h4 {
  font-family: 'MonoplexKR-Regular', sans-serif;
  margin-bottom: 1rem;
  color: #2c3e50;
}

.instagram-link-new a {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.2rem;
  margin-top: 4rem;
  border: 1.5px solid #e1306c;
  border-radius: 6px;
  color: #e1306c;
  font-weight: 600;
  font-size: 1rem;
  font-family: 'S-CoreDream-6Bold', sans-serif;
  transition: all 0.3s ease;
  text-decoration: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.instagram-link-new a:hover {
  background: #e1306c;
  color: #ffffff;
  box-shadow: 0 4px 12px rgba(225, 48, 108, 0.2);
}

.instagram-icon {
  width: 1.3rem;
  height: 1.3rem;
}

/* ---------- Home: Dog Info Table ---------- */

.dog-profile-title {
  font-size: 1.5rem;
  font-family: 'CoreDream', sans-serif;
  color: #141414;
  margin-top: 2rem;
  margin-bottom: 3rem;
  text-align: center;
}

.dog-specs {
  width: 100%;
  background: #fff0f5;
  border-radius: 1.5rem;
  border: 2px solid #ffc4d6;
  box-shadow: 0 4px 16px rgba(255, 105, 180, 0.15);
  overflow: hidden;
  font-family: 'S-CoreDream-3Light', sans-serif;
  border-spacing: 0;
  margin: 2rem auto;
}

.dog-specs th {
  background: #ffe2ec;
  color: #b23a48;
  padding: 1rem;
  font-weight: bold;
  font-size: 1rem;
  text-align: left;
  border-bottom: 1px dashed #fcb6c5;
}

.dog-specs td {
  background: #ffffff;
  color: #333;
  padding: 1rem;
  font-size: 0.95rem;
  border-bottom: 1px dashed #fcb6c5;
}

.dog-specs tr:last-child th,
.dog-specs tr:last-child td {
  border-bottom: none;
}


